.post-content {
    max-width: 800px;
    margin: 0 auto;
    padding: 2em;
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    font-family: 'Arial', sans-serif;
    line-height: 1.6;
  }
  
  .post-content h1,
  .post-content h2,
  .post-content h3,
  .post-content h4,
  .post-content h5,
  .post-content h6 {
    margin-top: 1em;
    font-weight: 600;
    line-height: 1.2;
  }

  .post-content h1 {
    color: rgb(255, 84, 84);
  }
  .post-content h2 {
    color: rgb(255, 161, 84);
  }
  .post-content h3 {
    color: rgb(220, 209, 0);
  }
  .post-content h4 {
    color: rgb(119, 255, 0);
  }
  
  .post-content p {
    margin: 1em 0;
  }
  
  .post-content a {
    color: #1e90ff;
    text-decoration: none;
  }
  
  .post-content a:hover {
    text-decoration: underline;
  }
  
  .post-content ul,
  .post-content ol {
    padding-left: 2em;
    margin: 1em 0;
  }
  
  .post-content blockquote {
    margin: 1.5em 0;
    padding: 0.5em 1em;
    background: #f9f9f9;
    border-left: 0.25em solid #ccc;
  }
  
  .post-content code {
    background: #f4f4f4;
    padding: 0.2em 0.4em;
    border-radius: 4px;
    font-family: 'Courier New', Courier, monospace;
  }
  
  .post-content pre {
    background: #f4f4f4;
    padding: 1em;
    border-radius: 8px;
    overflow-x: auto;
    font-family: 'Courier New', Courier, monospace;
  }
  
  .post-content img {
    max-width: 100%;
    height: auto;
    display: block;
    margin: 1em 0;
  }
  